.popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.2);

	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 9999;
}
.popup-inner {
	text-align: center;
	position: relative;
	padding: 32px;
	width: 100%;
	max-width: 640px;
	background-color: whitesmoke;
}
.popup-inner .close-btn {
	position: absolute;
	width: 5%;
	height: 5%;
}



